<h1 id="二维码" tabindex="-1"><a class="header-anchor" href="#二维码" aria-hidden="true">#</a> 二维码</h1>
<p>目前有 2 种类型的二维码：</p>
<ol>
<li>临时二维码，是有过期时间的，最长可以设置为在二维码生成后的 <strong>30天</strong>后过期，但能够生成较多数量。临时二维码主要用于帐号绑定等不要求二维码永久保存的业务场景</li>
<li>永久二维码，是无过期时间的，但数量较少（目前为最多10万个）。永久二维码主要用于适用于帐号绑定、用户来源统计等场景。</li>
</ol>
<h2 id="创建临时二维码" tabindex="-1"><a class="header-anchor" href="#创建临时二维码" aria-hidden="true">#</a> 创建临时二维码</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token property">qrcode</span><span class="token operator">-></span><span class="token function">temporary</span><span class="token punctuation">(</span><span class="token string single-quoted-string">'foo'</span><span class="token punctuation">,</span> <span class="token number">6</span> <span class="token operator">*</span> <span class="token number">24</span> <span class="token operator">*</span> <span class="token number">3600</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token comment">// Array</span>
<span class="token comment">// (</span>
<span class="token comment">//     [ticket] => gQFD8TwAAAAAAAAAAS5odHRwOi8vd2VpeGluLnFxLmNvbS9xLzAyTmFjVTRWU3ViUE8xR1N4ajFwMWsAAgS2uItZAwQA6QcA</span>
<span class="token comment">//     [expire_seconds] => 518400</span>
<span class="token comment">//     [url] => http://weixin.qq.com/q/02NacU4VSubPO1GSxj1p1k</span>
<span class="token comment">// )</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br></div></div><h2 id="创建永久二维码" tabindex="-1"><a class="header-anchor" href="#创建永久二维码" aria-hidden="true">#</a> 创建永久二维码</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token property">qrcode</span><span class="token operator">-></span><span class="token function">forever</span><span class="token punctuation">(</span><span class="token number">56</span><span class="token punctuation">)</span><span class="token punctuation">;</span><span class="token comment">// 或者 $app->qrcode->forever("foo");</span>
<span class="token comment">// Array</span>
<span class="token comment">// (</span>
<span class="token comment">//     [ticket] => gQFD8TwAAAAAAAAAAS5odHRwOi8vd2VpeGluLnFxLmNvbS9xLzAyTmFjVTRWU3ViUE8xR1N4ajFwMWsAAgS2uItZAwQA6QcA</span>
<span class="token comment">//     [url] => http://weixin.qq.com/q/02NacU4VSubPO1GSxj1p1k</span>
<span class="token comment">// )</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br></div></div><h2 id="获取二维码网址" tabindex="-1"><a class="header-anchor" href="#获取二维码网址" aria-hidden="true">#</a> 获取二维码网址</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$url</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token property">qrcode</span><span class="token operator">-></span><span class="token function">url</span><span class="token punctuation">(</span><span class="token variable">$ticket</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token comment">// https://api.weixin.qq.com/cgi-bin/showqrcode?ticket=TICKET</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><h2 id="获取二维码内容" tabindex="-1"><a class="header-anchor" href="#获取二维码内容" aria-hidden="true">#</a> 获取二维码内容</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$url</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token property">qrcode</span><span class="token operator">-></span><span class="token function">url</span><span class="token punctuation">(</span><span class="token variable">$ticket</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token variable">$content</span> <span class="token operator">=</span> <span class="token function">file_get_contents</span><span class="token punctuation">(</span><span class="token variable">$url</span><span class="token punctuation">)</span><span class="token punctuation">;</span> <span class="token comment">// 得到二进制图片内容</span>

<span class="token function">file_put_contents</span><span class="token punctuation">(</span><span class="token constant">__DIR__</span> <span class="token operator">.</span> <span class="token string single-quoted-string">'/code.jpg'</span><span class="token punctuation">,</span> <span class="token variable">$content</span><span class="token punctuation">)</span><span class="token punctuation">;</span> <span class="token comment">// 写入文件</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br></div></div>